@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@400;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
input:focus {
  outline: none;
}
a {
  color: inherit;
  text-decoration: none;
}


:root {
  --main-color: #ef7a1a;
  --main-black: #343a40;
}

.All {
  font-family: "Nanum Gothic", sans-serif;
  color: #343a40;
  position: relative;
  width: 100%;
}

/* BTN */
.btn {
  height: 34px;
  background: #ef7a1a;
  background: var(--main-color);
  border: none;
  border-radius: 99px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.5;
  cursor: pointer;
  box-sizing: border-box;
  position: relative;
  color: #fff;
}

.btn:hover {
  background: #fff0f0;
  color: #ef7a1a;
  color: var(--main-color);
  border: 1px solid #ef7a1a;
  border: 1px solid var(--main-color);
  transition: 0.5s;
}

.addbtn {
  background: #ef7a1a;
  background: var(--main-color);
  color: #fff0f0;
  border-radius: 99px;
  cursor: pointer;
  border: none;
  transition: 0.3s;
}

.addbtn:hover {
  background: #fff0f0;
  color: #ef7a1a;
  color: var(--main-color);
}

/* input */
input {
  font-family: "Nanum Gothic", sans-serif;
}

input::-webkit-input-placeholder {
  color: #ef7a1a;
  color: var(--main-color);
  font-family: "Nanum Gothic", sans-serif;
  text-align: center;
  font-size: 1rem;
  font-weight: 700;
}

input:-ms-input-placeholder {
  color: #ef7a1a;
  color: var(--main-color);
  font-family: "Nanum Gothic", sans-serif;
  text-align: center;
  font-size: 1rem;
  font-weight: 700;
}

input::placeholder {
  color: #ef7a1a;
  color: var(--main-color);
  font-family: "Nanum Gothic", sans-serif;
  text-align: center;
  font-size: 1rem;
  font-weight: 700;
}

/* Box */
.shadowBox {
  border: 1px solid #ddd;
  border-radius: 15px;
  box-shadow: rgb(100 100 111 / 20%) 0px 7px 29px 0px;
}


